import React, { useContext } from 'react'
import "./styles/Login.css"
import {useState} from 'react'
// axios library allows to handle api requests
import {Link, useNavigate} from "react-router-dom"
import axios from "axios";
import { AuthContext } from '../context/authContext';
// import 
const Login = () => {
  const [inputs, setInputs]= useState({
    username:"",
    password:""
    
});


  const navigate = useNavigate()

  const {login}=useContext(AuthContext)
  const {currentUser}=useContext(AuthContext)
  const [err,setError] =  useState(null)

  console.log("currentUser: ",currentUser)

  const handleChange = e =>{
    setInputs(prev=>({...prev, [e.target.name]: e.target.value}))
  }
  console.log("here",inputs)

  // async function to make api request
  const handleSubmit =async e =>{
    // to not let the page refresh on click of register button
    e.preventDefault()
    try{
      await login(inputs)
      navigate("/");  
    }catch(err){
      console.log(err)
      setError( err.response.data);
    }

  }
  return (
    <div className="auth">
        <h1 className="h1">Login</h1>
        <form className="form">
            <input className="input1" type="text" name="username" placeholder="username" onChange={handleChange}/>
            <input className="input2" type="text" name="password" placeholder="password" onChange={handleChange}/>
            <button onClick={handleSubmit} className="Loginbutton">Login</button>
            {err && <p>{err}</p>}
            <span>Don't have an Account? <Link to="/Register">Register</Link>
            </span>
        </form>
    </div>
  )
}

export default Login
