import React from 'react'
import {useState} from 'react'
import "./styles/Login.css"
// axios library allows to handle api requests
import {Link, useNavigate} from "react-router-dom"
import axios from "axios";

// import 

const Register = () => {
  const [inputs, setInputs]= useState({
    username:"",
    email:"",
    password:""
    
  });

const navigate = useNavigate()

const [err,setError] =  useState(null)

const handleChange = e =>{
  setInputs(prev=>({...prev, [e.target.name]: e.target.value}))
}
console.log(inputs)

// async function to make api request
const handleSubmit =async e =>{
  // to not let the page refresh on click of register button
  e.preventDefault()
  console.log("here0")
  // axios.post("http://localhost:8800/api/auth/register",inputs)
  axios.post("/auth/register",inputs)

  .then(() => {
    console.log("request sent");
    navigate("/login");  
    console.log("here1")
  }).catch((err) => {
    console.log("error")
    console.log(err);
  })
    
  
  // try{
  //   await axios.post("http://localhost:8800/api/auth/register",inputs)
  //   navigate("/login");  
  //   console.log("here1")
  // }catch(err){
  //   console.log("here2")
  //   console.log(err)
  //   setError(err.response.data);
  // }

}
  return (
    <div className="auth">
        <h1 className="h1">Register</h1>
        <form className="form">
            <input required className="input1" type="text" placeholder="username" name="username" onChange={handleChange}/>
            <input required className="input2" type="email" placeholder="email" name="email" onChange={handleChange}/>
            <input required className="input2" type="text" placeholder="password" name="password" onChange={handleChange}/>
            <button onClick={handleSubmit} className="Loginbutton">Start</button>
             {err && <p>{err}</p>}
            <span>Do you have an Account? <Link to="/Login">Login</Link>
            </span>
        </form>
    </div>

  );
}

export default Register
