import React from "react";
import { createContext, useEffect, useState } from "react";
// axios library allows to handle api requests
import axios from "axios";
import {Link, useNavigate} from "react-router-dom"



// AuthContext  will have user information
export const AuthContext = createContext();

// the children in context provider is the components we are going to share the user with,we use this iindex.js
export const AuthContextProvider=({children})=>{
    

    const [currentUser, setCurrentUser] = useState(JSON.parse(localStorage.getItem("user"))|| null
    );

    const login= async(inputs)=>{
        const res=await axios.post("auth/login",inputs);
        setCurrentUser(res.data);
    };

    const logout= async(inputs)=>{
        const res=await axios.post("auth/logout");
        setCurrentUser(null);
        
    };
// to update local storrage each time we change user
    useEffect(()=>{
         localStorage.setItem("user",JSON.stringify(currentUser))
    },[currentUser]);

    return  (
        <AuthContext.Provider value={{currentUser, login, logout}}>
            {children}
        </AuthContext.Provider>
    );
};
