import React, { useState, useEffect,useContext } from 'react';
import ReactQuill from "react-quill";
import 'react-quill/dist/quill.snow.css';
import axios from 'axios';
import {Link,useNavigate, useLocation} from "react-router-dom"
import moment from 'moment';

const Write = () => {
  const state = useLocation().state;

  const [value, setValue] = useState("");
  const [title, setTitle] = useState("");
  const [file, setFile] = useState(null);
  const [cat, setCat] = useState("");
  useEffect(() => {
    if (state) {
      setTitle(state.title);
      setValue(state.desc);
      setCat(state.cat);
    }
    else{
    setTitle("");
      setValue("");
      setCat("");
    }
  }, [state]);

  const navigate = useNavigate()

  // const setDescription = (value) => {
  //   console.log("value from react quill",value.replace(/<\/?[^>]+(>|$)/g, ""));
  //   setValue(value.replace(/<\/?[^>]+(>|$)/g, ""))
  // }
  

  const upload= async() =>{
    try{
      const formData=new FormData();
      console.log("file", file);
      formData.append("file",file)
      const res = await axios.post("/upload",formData)
      return res.data
    } catch(err){
      console.log(err)
    }
  }
  const handleClick= async e =>{
    let imgURL;
    e.preventDefault()
    file ? (
      imgURL = await upload()
     ) : (
      imgURL = state.img
     )
     console.log("imgURL from backend/original:", imgURL,cat,value,title)
    // how to uplaod files inside server use multer
    try{
      state ? await axios.put(`/posts/${state.id}`, {
          title,
          desc: value,
          cat,
          img: file ? imgURL : "",
        })
      : await axios.post(`/posts/`, {
          title,
          desc: value,
          cat,
          img: file ? imgURL : "",
          date: moment(Date.now()).format("YYYY-MM-DD HH:mm:ss"),
        });
        navigate("/")
      
    }
    catch(err){
      console.log("write function ::",err)
  }
 }

  return (
    <div className="add">
      <div className="content">
        <input type="text" value={title} placeholder="Title" onChange={e=>setTitle(e.target.value)}/>
        <div className="editorContainer">
          <ReactQuill  className="editor" theme='snow' value={value} onChange={setValue}/>
        </div>
      </div>
      

      <div className="menuwrite">

        <div className="item">
          <h1>Publish</h1>
          <span>
            <b>Status: </b>Draft
          </span>
          <span>
            <b>VisibilityL: </b>Public
          </span>
          <input style={{display:"none"}} type="file" name="file" id="file" onChange={e=>setFile(e.target.files[0])}></input>
          <label className="file" htmlFor="file">Upload Image</label>
          <div className="buttons">
            <button >Save as a Draft</button>
            <button onClick ={handleClick}>Publish</button>
          </div>
        </div>

        <div className="item">
          <h1>Category</h1>
          <div className="cat">
          <input type ="radio" checked={cat === "art"} name="cat" value="art" id="art" onChange={e=>setCat(e.target.value)}/>
          <label htmlFor="art">Art</label>
          </div>
          <div className="cat">
          <input type ="radio" checked={cat === "science"}   name="cat" value="science" id="science" onChange={e=>setCat(e.target.value)}/>
          <label htmlFor="science">Science</label>
          </div>

          <div className="cat">
          <input type ="radio" checked={cat === "technology"} name="cat" value="technology" id="technology" onChange={e=>setCat(e.target.value)}/>
          <label htmlFor="technology">Technology</label>
          </div>
          <div className="cat">
          <input type ="radio" checked={cat === "cinema"} name="cat" value="cinema" id="cinema" onChange={e=>setCat(e.target.value)}/>
          <label htmlFor="cinema">Cinema</label>
          </div>
          <div className="cat">
          <input type ="radio"  checked={cat === "design"} name="cat" value="design" id="design" onChange={e=>setCat(e.target.value)}/>
          <label htmlFor="design">Design</label>
          </div>
          <div className="cat">
          <input type ="radio"  checked={cat === "food"} name="cat" value="food" id="food" onChange={e=>setCat(e.target.value)}/>
          <label htmlFor="food">Food</label>
          </div>
        </div>
      </div>

    </div>

  )
}

export default Write
