import React, { useContext,useState ,useEffect} from 'react'

import imag from "../img/life1.jpeg"
import joimg from "../img/joimg.jpg"
import Edit from "../img/edit1.png"
import Delete from "../img/delete1.png"
import {Link,useNavigate, useLocation} from "react-router-dom"
import Menu from "../components/Menu"
import axios from 'axios'
import moment from "moment"
import { AuthContext } from '../context/authContext'

const Single = () => {
  const {currentUser}=useContext(AuthContext)
  const [post,setPost] = useState([])

  const location = useLocation()
  const postId = location.pathname.split("/")[2]
  console.log("postId",postId)
  const navigate=useNavigate()

  useEffect(()=>{
    const fetchData = async ()=>{
      try{
        console.log(postId);
        const res=await axios.get(`/posts/${postId} `)
        console.log("res hererere",res.data)
        setPost(res.data); 
      }catch(err){
        console.log("*************************",err)
      }
    };
    fetchData();
  },[postId])

  const handleDelete=async () =>{
    try{
      const res=await axios.delete(`/posts/${postId}`)
      console.log("res",res.data)
      navigate("/")
    }catch(err){
      console.log(err)
    }
  }

  const getText =(html) =>{
    const doc =new DOMParser().parseFromString(html,"text/html")
    return doc.body.textContent
  }

  return (
    <div className="single">
      <div className ="content">
         {post.img && <img src= {`../upload/${post.img}`} alt=""></img>}
        
          <div className="user">

            {post.userimg &&<img src={post.userimg}  alt=""></img>}
            <div className="info">
              <span>{post.username}</span>
              <p class="nm">Posted {moment(post.date).fromNow()}</p>
            </div>
            {currentUser.username === post.username &&(
              <div className="edit">
                <Link to={`/write?edit=2`} state={post}><img src={Edit}  alt=""></img></Link>
                <Link ><img onClick={handleDelete} src={Delete} alt=""></img></Link>
              </div>
            )}   

          </div> 
          <h1>{post.title}</h1>
          {getText(post.desc)}
        </div>
      <Menu cat={post.cat}/>
    </div>

  );
};

export default Single
